@use "../../../variables" as var;
.CustomModal {
  :global(.ant-modal-content) {
    box-sizing: border-box;
    // background-color: rgba(#070b2d, 1);
    background-color: white;
    // border-radius: 24px;
    padding: 0;
  }
  :global(.ant-modal-body) {
    padding: 0;
    color: var.$color-main-text;
  }
}

.titleWrap {
  position: absolute;
  top: 15px;
  left: 0;
  width: 100%;
  display: flex;
  // flex-direction: column;
  // align-items: flex-start;
  img {
    width: 15px;
    cursor: pointer;
    margin-right: 15px;
  }
  h2 {
    width: 100%;
    text-align: center;
    font-size: 24px;
    font-weight: 700;
    margin: 0;
    text-align: left;
  }
}
