@use "../../variables" as var;

.Investment {
  position: relative;
  width: 100%;
  display: flex;
}

.imgWrap,
.contentWrap {
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.imgWrap {
  img {
    width: 100%;
  }
}

.contentWrap {
  padding: 15px;
}
.investWrap {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 390px;
  gap: 40px;
  text-align: left;
}
.title {
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: var.$color-main-text;
  margin: 0;
}
.inputItem {
  width: 100%;
  h2 {
    color: var.$color-main-text;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 10px;
  }
  p {
    margin: 10px 0 0 5px;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: var.$color-main-text;
  }
  a {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-decoration-line: underline;
    color: var.$color-main-text;
  }
}
.submitButton {
  padding: 10px 40px;
}

:global(.ant-input-affix-wrapper) {
  border-radius: 10px;
  // background-color: red;
}

:global(.ant-input) {
  border-radius: 0;
}

@media (max-width: 765px) {
  .Investment {
    flex-direction: column;
    gap: 20px;
  }
  .imgWrap {
    img {
      width: 60%;
    }
  }
  .investWrap {
    gap: 30px;
  }
}
