@use "../../../../variables" as var;

:global(.ant-progress-inner) {
  outline: 1px solid #fdc231;
}

.InvestmentItem {
  position: relative;
  width: 100%;
  display: flex;
  gap: 20px;
  border-bottom: 1px solid rgba(var.$color-main-text, 0.5);
  padding: 30px 0;
}

.infoItem {
  display: flex;
  align-items: center;
  gap: 10px;
  h2,
  p {
    margin: 0;
    font-size: 16px;
    line-height: 19px;
    color: var.$color-main-text;
  }
  h2 {
    font-weight: 600;
    white-space: nowrap;
  }
  p {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}

.statsWrap {
  border: 1px solid rgba(var.$color-main-text, 0.5);
  border-radius: 10px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  gap: 10px;
  width: 40%;
  .infoItem {
    justify-content: space-between;
  }
}

.unlockWrap {
  width: 60%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  .infoItem {
    justify-content: space-between;
  }
}

.progressWrap {
  .infoItem {
    margin-bottom: 10px;
  }
}

@media (max-width: 765px) {
  .InvestmentItem {
    flex-direction: column;
  }
  .statsWrap,
  .unlockWrap {
    width: 100%;
  }
}
