.team {
  background-color: #f4f4f4;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
  padding-top: 20px;

  .square {
    position: relative;
    width: 100%;
    max-width: 140px;

    .content {
      width: 100%;
    }
  }

  .teamContent {
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 10px;

    h1,
    h3 {
      // margin-bottom: -10px;
      margin: 0;
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
    }

    > P {
      margin: 10px 0;
      text-align: left;
    }
  }
}

@media (max-width: 765px) {
  .team {
    width: 100%;
    margin-top: 20px;
  }
}
