@use "../../../variables" as var;
.ProfileButton {
}
.signupButton,
.profileLink {
  cursor: pointer;
  font-weight: 500;
  font-size: 16px;
  line-height: 23px;
  color: var.$color-main-text;
  border-top: 3px solid transparent;
  border-bottom: 1px solid transparent;
}
.profileLink {
  text-decoration: none;
  &:hover,
  &.active {
    border-bottom: 1px solid var.$color-main-text !important;
    color: var.$color-main-text;
  }
}
