.footer {
  background-color: #515151;
  width: 100%;
  .footInner {
    max-width: 1500px;
    display: flex;
    justify-content: center;
    padding: 30px 0 10px;
    .contactInfo {
      color: #fff;
      font-weight: 400;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 5px;
      > p {
        font-size: 14px;
        margin: 0;

        > a {
          color: unset;
        }
      }
      > h1 {
        margin: 0;
        color: #fff;
        font-weight: 900;
      }
    }
  }
  .contactForm {
    text-align: left;
    color: #fff;
    .contactInput {
      display: flex;
      flex-direction: column;
      .nameInfo {
        display: flex;
        justify-content: space-between;
      }
      .contactDetail {
        display: flex;
        justify-content: space-between;
      }
    }
  }
}
.hide {
  display: none;
}
@media (max-width: 900px) {
  .footer {
    .footInner {
      padding: 10px 0 10px;
      .contactInfo {
        gap: 5px;
      }
      .contactForm {
        .contactInput {
          flex-direction: column;
          .nameInfo,
          .contactDetail {
            flex-direction: column;
          }
        }
      }
    }
  }
}
