@use "../../../variables" as var;

.Button {
  position: relative;
  min-width: 60px;
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  line-height: 22px;
  padding: 10px 15px;
  cursor: pointer;
  user-select: none;
  white-space: nowrap;
  &.default {
    background: var.$gradient-bg;
    color: white;
    border-radius: 10px;
    border: none;
    box-shadow: 0 20px 20px -15px rgb(253, 200, 48);
    font-weight: 700;
  }
  &.secondary {
    background: none;
    background-color: white;
    border: 1px solid #000000;
    color: var.$color-main-text;
    font-weight: 500;
    box-shadow: none;
  }
  &.noBorderRadius {
    border-radius: 0;
  }
  &.disabled {
    opacity: 0.8;
    filter: grayscale(1);
    // pointer-events: none;
  }
}

@media (max-width: 765px) {
  .Button {
    font-size: 14px;
    line-height: 20px;
  }
}
