.placeholder {
  position: relative;
  background-color: transparent;
  height: 70px;
}

.Header {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  background-color: white;
  z-index: 1;
  padding: 10px;
}
.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
}
.logo {
  height: 50px;
}
.hamburger {
  font-size: 25px;
}
.langButton {
  height: 25px;
  cursor: pointer;
}
.menuWrap {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}
.navWrap {
  display: flex;
  column-gap: 20px;
}
.link {
  font-size: 16px;
  font-weight: 400;
  line-height: 23px;
  color: #5e5a5a;
  border-top: 3px solid transparent;
  border-bottom: 1px solid transparent;
  cursor: pointer;
  &:hover,
  &.active {
    border-bottom: 1px solid #5e5e5e;
    font-weight: 500;
    color: #5e5a5a;
  }
}
.HeaderMenuWrapper {
  :global(.ant-modal-content) {
    box-sizing: border-box;
    background: white;
    // border: 1px;
    border-radius: 10px;
    padding: 0;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    // font-family: "Battambang", sans-serif;
  }
  :global(.ant-modal-body) {
    padding: 15px 20px;
  }
}

.verticalBar {
  border-left: 1px solid #5e5a5a;
}
.walletWrap {
  display: flex;
  align-items: center;
  gap: 10px;
}

@media (max-width: 765px) {
  .menuWrap {
    height: 80vh;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;
  }
  .navWrap,
  .walletWrap {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .verticalBar {
    display: none;
  }
}
