@use "../../../variables" as var;
.ProfileReferral {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 40px;
  hr {
    border: none;
    border-top: 1px solid rgba(var.$color-main-text, 0.5);
  }
}

.title {
  font-weight: 600;
  font-size: 24px;
  line-height: 28px;
  color: var.$color-main-text;
}

.infoItem {
  display: flex;
  align-items: center;
  gap: 10px;
  h2,
  p {
    margin: 0;
    font-size: 16px;
    line-height: 19px;
    color: var.$color-main-text;
  }
  h2 {
    font-weight: 600;
    white-space: nowrap;
  }
  p {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}

.addressCopyButton {
  padding: 5px 10px;
  margin-left: 10px;
}

.referralInfo {
  display: flex;
  // align-items: flex-start;
  gap: 20px;
}
.statsWrap {
  width: 40%;
  display: flex;
  flex-direction: column;
}
.codeWrap {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  gap: 10px;
}
.statsContent {
  border: 1px solid rgba(var.$color-main-text, 0.5);
  border-radius: 10px;
  padding: 15px;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  gap: 10px;
  .infoItem {
    justify-content: space-between;
  }
}
.codeInfo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  .infoAndCopy {
    width: 100%;
    display: flex;
    gap: 20px;
    p {
      border: 1px solid rgba(var.$color-main-text, 0.5);
      border-radius: 10px;
      flex: 1;
      padding: 10px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
}
.claimButton {
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 765px) {
  .referralInfo {
    flex-direction: column;
  }
  .statsWrap,
  .codeWrap {
    width: 100%;
  }
}
