// @import url(//db.onlinewebfonts.com/c/c5bd605b45237a12c99133fa9d7cae49?family=HelveticaW01-Roman);
.foreignExchange {
  p,
  li {
    font-size: 16px;
    line-height: 1.8em;
    font-family: helvetica-w01-roman, helvetica-w02-roman,
      helvetica-lt-w10-roman, sans-serif;
    color: #333333;
    font-weight: 350;
  }
  h2 {
    font-size: 24px;
    font-weight: 700;
  }
  > img {
    width: 100%;
  }
  > h1 {
    font-size: 56px;
  }

  h1,
  h2,
  p,
  li {
    text-align: left;
  }
  .feContent {
    display: flex;
    justify-content: flex-start;
    gap: 200px;
    .feIntro {
      width: 70%;
      > h1 {
        font-size: 56px;
      }
    }
  }

  ul {
    list-style-type: none;
  }
}
@media (max-width: 765px) {
  .foreignExchange {
    > img {
      height: 20vh;
    }
    .feContent {
      flex-direction: column;
    }
  }
}
