.product {
  width: 100%;
  background-color: rgb(250, 250, 250);
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  > img {
    width: 100%;
  }
  .productContent {
    height: 50%;
    padding: 15px 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 220px;
    gap: 10px;
    h3 {
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
      margin: 0;
    }
    > p {
      // text-align: justify;
      text-align: left;
    }
    .more {
      align-self: flex-end;
      margin-top: auto;
      cursor: pointer;
      > a {
        color: unset;
      }
    }
  }
}
@media (max-width: 765px) {
  .product {
    width: 100%;
    margin-top: 20px;

    .productContent {
      align-items: center;
      min-height: unset;
    }
  }
}
