@use "../../variables" as var;
.Profile {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex: 1;
  gap: 20px;
  text-align: left;
}
.title {
  margin: 0;
}
hr {
  border: none;
  border-top: 1px solid rgba(var.$color-main-text, 0.5);
}
.contentWrap {
  margin-top: 20px;
  position: relative;
  width: 100%;
  display: flex;
  gap: 15px;
}
.tabList {
  padding: 0 20px;
  width: 20%;
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.tabItem {
  width: 100%;
  padding: 8px 15px;
  border-radius: 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: var.$color-main-text;
  cursor: pointer;
  &:hover,
  &.active {
    background: #fffaeb;
  }
}
.profileDetailWrap {
  width: 80%;
  // flex: 1;
}

@media (max-width: 765px) {
  .contentWrap {
    flex-direction: column;
    gap: 25px;
  }
  .tabList {
    width: 100%;
    padding: 0;
  }
  .profileDetailWrap {
    width: 100%;
    // flex: 1;
  }
}
