@use "../../../variables" as var;
.Signup {
  display: flex;
  align-items: center;
}
.imgWrap {
  width: 40%;

  img {
    width: 100%;
  }
}
.contentWrap {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 45px 0;
}
.signupWrap {
  width: 100%;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  .title {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: black;
  }
  .button {
    margin-top: 20px;
    align-self: center;
    width: 149px;
    height: 48px;
  }
  .inputItem {
    h2 {
      color: var.$color-main-text;
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
      margin-bottom: 5px;
    }
  }
  :global(.ant-input-affix-wrapper) {
    border-radius: 10px;
    // background-color: red;
  }

  :global(.ant-input) {
    border-radius: 10px;
  }
}
