.home {
  width: 100%;
  overflow: hidden;
  .banner {
    width: 100%;
    height: calc(100vh - 70px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    gap: 30px;
    box-shadow: inset 0 0 0 50vw rgba(255, 255, 255, 0.35);
    .bannerContent {
      > h1 {
        font-weight: 900;
        font-size: 64px;
        line-height: 70px;
        margin-bottom: 20px;
        color: rgba(0, 0, 0, 1);
      }

      > h3 {
        font-weight: 600;
        font-size: 21px;
        line-height: 25px;
      }
    }
  }
  .mainContent {
    > img {
      display: inline-block;
      width: 100%;
      height: 191px;
      margin-bottom: 50px;
    }
    .imgWrap {
      width: 100%;
      height: 200px;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      overflow: hidden;
      margin-bottom: 50px;
    }
    .about {
      width: 100%;
      padding: 0 200px;
      display: flex;
      align-items: center;
      flex-direction: column;
      gap: 20px;
      margin-bottom: 50px;
      > h1 {
        color: #5e5a5a;
        font-size: 24px;
        font-weight: 700;
        margin: 0;
      }
      > p {
        font-size: 14px;
        line-height: 150%;
        color: #5e5a5a;
        margin: 0;
        text-align: justify;
        font-family: helvetica-w01-roman, helvetica-w02-roman,
          helvetica-lt-w10-roman, sans-serif;
      }
      // > button {
      //   width: 136px;
      //   height: 44px;
      //   background-color: transparent;
      //   cursor: pointer;
      //   border: solid rgba(51, 51, 51, 1) 1px;
      //   margin-bottom: 50px;
      //   > a {
      //     color: unset;
      //     font-weight: 500;
      //     font-size: 18px;
      //   }
      // }
      // > button:hover {
      //   background: #515151;
      //   color: white;
      // }
    }
    .products {
      letter-spacing: 0.02em;

      > h1 {
        color: #5e5a5a;
        font-size: 50px;
        margin-bottom: 50px;
      }

      .productWrapper {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        gap: 0.5%;
      }
    }
  }
  .team {
    max-width: 1300px;
    padding-top: 0;
    > h1 {
      font-size: 24px;
      font-weight: 700;
      margin-bottom: 40px;
      color: #5e5a5a;
    }
    .teamWrapper {
      display: flex;
      gap: 1%;
    }
  }
}
.scrollToTop {
  z-index: 2;
  position: fixed;
  right: 20px;
  bottom: 3vh;
  cursor: pointer;
}
@media (max-width: 765px) {
  .home {
    .banner {
      justify-content: flex-start;
      .bannerContent {
        > h1 {
          font-size: 36px;
          line-height: 40px;
        }
        > h3 {
          font-size: 18px;
          line-height: 20px;
        }
      }
    }
    .mainContent {
      .about {
        width: 100%;
        padding: 0;
        > h1 {
          font-size: 24px;
        }
      }
      .products {
        > h1 {
          font-size: 24px;
        }
      }
    }
    .team {
      > h1 {
        font-size: 24px;
      }
      .teamWrapper {
        flex-direction: column;
      }
    }
  }
}
